import * as React from "react";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";

import Image from "./img/ringo.jpg";

export default function ImgMediaCard() {
  return (
    <Card sx={{ maxWidth: 345 }}>
      <CardMedia
        component="img"
        alt="green iguana"
        height="140"
        image={Image}
      />
      <CardContent>
        <Typography gutterBottom variant="h5" component="div">
          リンゴ
        </Typography>
        <Typography variant="body2" color="text.secondary">
          1kg:￥1000
        </Typography>
        <Typography variant="body2" color="text.secondary">
          商品詳細
        </Typography>
        <Typography variant="body2" color="text.secondary">
          りんごは、強い健康パワーを持つとして有名な果物です。
          クエン酸やリンゴ酸、カリウム、食物繊維の一種であるペクチン、強い抗酸化力を持つりんごポリフェノールなどが豊富に含まれており、生活習慣病の予防や整腸効果、美肌効果など多岐にわたる効果が期待されています。
        </Typography>
      </CardContent>
      <CardActions>
        <Button size="small">カートに追加</Button>
        <Button size="small">今すぐ購入</Button>
      </CardActions>
    </Card>
  );
}
